input[type="text"],
input[type="url"],
input[type="email"],
input[type="number"],
input[type="password"],
input[type="search"],
textarea {
  font-size: 16px;
  line-height: normal;
}

@keyframes slideLeft {
  0% {
    transform: translateX(0%);
  }
  100% {
    transform: translateX(-100%);
  }
}

/* 平板適配 */
@media screen and (min-width: 481px) and (max-width: 1024px) {
  .input-row {
    display: flex;
    align-items: center;
    padding: 12px;
    gap: 12px;
  }

  .paste-icon,
  .save-icon {
    width: 32px;
    height: 32px;
  }

  input[type="url"] {
    width: calc(100% - 200px);
  }
}

/* 針對最小寬度為 768px 的 */
@media (min-width: 768px) {
  .body {
    flex-direction: row;
    justify-content: space-between;
  }

  .mainBody {
    width: calc(100% - 120px);
    margin: 20px 30px;
  }

  .ad {
    display: flex;
    align-items: center;
    flex-direction: column;
    width: 100px;
  }

  .tablinks,
  .subtablinks {
    flex: 1;
  }

  .footer-main {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin: 0px 100px;
    padding: 30px 20px;
  }

  .footer-left,
  .footer-right {
    width: auto;
    text-align: left;
    margin-bottom: 0;
  }

  .footer-left {
    font-size: 1.65vmin;
  }

  .footer-right {
    align-items: flex-start;
  }

  .copyright {
    font-size: 1.7vmin;
  }

  .marquee-container {
    right: -30%;
  }

  .top-animation {
    transform: scale(0.9);
    top: -5%;
    right: 5%;
  }

  .bottom-animation {
    transform: scale(0.9);
    bottom: 30%;
    right: 5%;
  }

  .marquee-word {
    /* font-size: 2rem; */
    right: 30%;
  }
}

/* 手機 */
@media (max-width: 480px) {
  .mainBody {
    width: calc(100% - 20px);
    margin: 20px 30px;
  }

  .bulletin-board {
    font-size: 0.9rem;
  }

  .input-row {
    display: flex;
    align-items: center;
    padding: 8px;
    gap: 8px;
    position: relative;
  }

  input[type="url"] {
    flex: 1;
    min-width: 0; /* 防止 flex 子項溢出 */
    font-size: 16px;
    padding: 10px;
  }

  .paste-icon,
  .save-icon {
    width: 24px;
    height: 24px;
    flex-shrink: 0; /* 防止圖標被壓縮 */
  }

  .save-icon-wrapper {
    position: relative;
    z-index: 2;
  }
}

  .marquee-container {
    right: -20%;
  }

  .top-animation {
    transform: scale(0.8);
    top: -10%;
    right: 8%;
  }

  .bottom-animation {
    transform: scale(0.65);
    bottom: 38%;
    right: 5%;
  }

  .marquee-word {
    font-size: 2rem;
    right: 20%;
  }

/* 針對橫屏模式 */
@media screen and (orientation: landscape) and (max-height: 480px) {
  .input-row {
    padding: 8px 16px;
  }

  input[type="url"] {
    padding: 8px 12px;
  }
}

/* 高 DPI 設備優化 */
@media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi) {
  input[type="url"] {
    border-width: 0.5px;
  }
}
